import firebase from "./firebaseInit";
const db = firebase.firestore();

export default {
  compareDates(a, b) {
    if (a.creationDate < b.creationDate) return -1
    if (a.creationDate > b.creationDate) return 1
    return 0
  },
  getJobs: function(jobs, callback) {
    let priorityJobs = [];
    let normalJobs = [];
    return db.collection("print_jobs").get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          "id": doc.id,
          "name": doc.data().name,
          "part": doc.data().part,
          "time": doc.data().print_time,
          "material": doc.data().material,
          "priority": doc.data().priority,
          "creationDate": doc.data().creationDate
        };
        if (data.priority === "high") priorityJobs.push(data);
        else normalJobs.push(data);
      });
      priorityJobs = priorityJobs.sort(this.compareDates);
      normalJobs = normalJobs.sort(this.compareDates);
      priorityJobs.forEach(job => jobs.push(job));
      normalJobs.forEach(job => jobs.push(job));
      callback();
    });
  },
  addJob: function(newJob, callback) {
    db.collection("print_jobs").add({
      name: newJob.name,
      part: newJob.part,
      print_time: newJob.time,
      material: newJob.material,
      priority: newJob.priority,
      creationDate: new Date()
    })
    .then(docRef => callback())
    .catch(err => console.error("Error adding new job: ", err))
  },
  removeJob: function(job, callback) {
    return db.collection("print_jobs").where("part", "==", job.part).where("name", "==", job.name).where("print_time", "==", job.time).where("creationDate", "==", job.creationDate).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        doc.ref.delete();
        callback();
      });
    });
  }
};


