<template>
  <div v-if="loggedIn" id="dashboard">
    <div class="w3-row">
      <div class="w3-col m7 w3-container w3-margin-bottom">
        <router-link id="add-button-top" to="/add" class="w3-border-blue w3-border w3-white w3-center w3-margin-bottom w3-text-blue w3-xxlarge">+</router-link>
        <ul id="print-job-list" class="w3-ul">

          <!--Loading Icon-->
          <li class="w3-center w3-section w3-animate-opacity" v-if="!jobsLoaded">
            <i class="fas fa-sync fa-spin w3-text-blue fa-2x"></i>
          </li>

          <!--Empty List-->
          <li class="w3-center w3-margin-top w3-animate-opacity" v-if="(jobsLoaded && jobs.length < 1)">
            <p class="w3-text-blue w3-large">...</p>
          </li>

          <!--Print Jobs-->
          <li v-for="job in jobs" class="w3-card print-job w3-animate-opacity" v-bind:class="{ 'priority-high': (job.priority == 'high'), 'priority-low': (job.priority != 'high') }">
            <button class="w3-button w3-hover-none w3-large w3-right" v-on:click="deleteJob(job)"><i class="fas fa-trash-alt"></i></button>
            <span class="w3-xlarge">
              {{ job.part }}
              <span v-if="job.priority == 'high'">
                <i class="fas fa-exclamation-circle"></i>
              </span>
            </span><br>
            Tulostaja: {{ job.name }} <br>
            Materiaali: {{ job.material }} <br>
            Aika: {{ Number(job.time) }}h
          </li>

        </ul>
      </div>
      <div class="w3-col m5 w3-container">
        <div class="w3-white w3-leftbar w3-border-blue w3-card w3- w3-padding w3-large w3-margin-bottom">
          <p><u>Yhteensä</u></p>
          <p>Kaikki: {{ timeEstimate }}h</p>
          <p>Kiireelliset: {{ priorityEstimate }}h <i class="fas fa-exclamation-circle w3-text-blue"></i></p>
          <p><u>Tulostin vapaana</u></p>
          <p>{{ estimatedDate.toLocaleDateString("fi", { weekday: "long", month: 'long', day: 'numeric' }) }} {{ estimatedDate.toLocaleTimeString("en-GB", { hour: "2-digit", minute: "2-digit" }) }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import printjobController from "./printjobController"
  import auth from "./auth"

  export default {
    name: 'dashboard',
    data () {
      return {
        loggedIn: false,
        jobs: [],
        jobsLoaded: false,
        timeEstimate: 0,
        priorityEstimate: 0,
        estimatedDate: new Date()
      }
    },
    created () {
      this.loggedIn = auth.checkAuth()
      if (!this.loggedIn) this.$router.push("/login")
      this.fetchJobs()
    },
    methods: {
      fetchJobs() {
        this.jobs = []
        this.jobsLoaded = false

        if (this.loggedIn) {
          printjobController.getJobs(this.jobs, () => {
            this.jobsLoaded = true

            this.timeEstimate = this.jobs.reduce((sum, cur) => sum + Number(cur.time), 0)
            this.priorityEstimate = this.jobs.reduce((sum, cur) => {
              if (cur.priority === "high") return sum + Number(cur.time)
              return sum + 0
            }, 0)
            this.checkEstimatedDate()
            setTimeout(this.fetchJobs, 40000)
          })
        }
      },
      deleteJob(clickedJob) {
        if (confirm("Oletko varma?")) {
          printjobController.removeJob(clickedJob, () => {
            this.fetchJobs();
          })
        }
      },
      checkEstimatedDate() {
        let curDate = new Date()
        curDate.setHours(curDate.getHours() + Number(this.timeEstimate))
        this.estimatedDate = curDate
      }
    }
  }
</script>
    
<style scoped>
#add-button-top {
  display: block;
  margin: 0 auto;
  padding: 8px;
  width: 100%;
  text-decoration: none;
  transition: background-color 0.1s;
}

#add-button-top:hover {
  background-color: var(--theme-primary-color) !important;
  color: var(--theme-secondary-color) !important;
}

li.print-job {
  margin-bottom: 8px;
  padding: 16px;
  border: 0 !important;
}

li.print-job > button {
  transition: opacity 0.1s;
}

li.priority-high {
  background-color: var(--theme-primary-color);
  color: var(--theme-secondary-color);
}

li.priority-high > button {
  background: none;
  color: var(--theme-secondary-color) !important;
}

li.priority-high > button:hover {
  background: none;
  opacity: 0.8;
}

li.priority-low {
  background-color: var(--theme-secondary-color);
  color: var(--theme-primary-color);
}

li.priority-low > button {
  background: none;
  color: var(--theme-primary-color) !important;
}

li.priority-low > button:hover {
  background: none;
  opacity: 0.8;
}
</style>
    


